import React from 'react';
import { SEO } from '../SEO';

interface LayoutProps {
  title?: string;
  description?: string;
  keywords?: Array<string>;
  imageURI?: string;
  language?: string;
  location: Location;
}

export const Layout: React.FC<LayoutProps> = ({
  children,
  title,
  description,
  keywords,
  imageURI,
  language,
  location,
}) => (
  <>
    <SEO
      title={title}
      description={description}
      keywords={keywords}
      imageURI={imageURI}
      language={language}
      location={location}
    />
    {children}
  </>
);
